import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useToast } from '@/hooks/use-toast';
import { auth } from '@/firebase';

const egyptOperators = [
  { label: 'Vodafone', value: 'Vodafone' },
  { label: 'Orange', value: 'Orange' },
  { label: 'Etisalat', value: 'Etisalat' },
  { label: 'WE', value: 'WE' },
];

const isValidEgyptPhone = (phone: string) => {
  const digits = phone.replace(/\D/g, '');
  return digits.length === 10 || digits.length === 11;
};

const TopUpPage: React.FC = () => {
  const navigate = useNavigate();
  const { toast } = useToast();
  const [phone, setPhone] = useState('');
  const [countryCode, setCountryCode] = useState('EG');
  const [operator, setOperator] = useState<string>('Vodafone');
  const [amount, setAmount] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [showConfirm, setShowConfirm] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!isValidEgyptPhone(phone)) {
      toast({ title: 'رقم غير صالح', description: 'من فضلك أدخل رقم مصري صحيح (10 أو 11 رقم)', variant: 'destructive' });
      return;
    }
    const amt = Number(amount);
    if (!Number.isFinite(amt) || amt <= 0) {
      toast({ title: 'قيمة غير صالحة', description: 'من فضلك أدخل مبلغ صحيح بالجنيه المصري', variant: 'destructive' });
      return;
    }
    setShowConfirm(true);
  };

  const confirmTopup = async () => {
    setShowConfirm(false);
    // تقييد استخدام زر الشحن على حساب محدد فقط
    const allowedEmail = 'alkaptin678678@gmail.com';
    const userEmail = auth.currentUser?.email?.toLowerCase();
    if (!userEmail || userEmail !== allowedEmail) {
      toast({ title: 'قيد الإنشاء', description: 'ميزة الشحن متاحة لهذا الحساب فقط حالياً.' });
      return;
    }
    setLoading(true);
    try {
      // إضافة مهلة للطلب لتجنب الانتظار اللانهائي
      const controller = new AbortController();
      const timeout = setTimeout(() => controller.abort(), 15000);

      const idToken = await auth.currentUser?.getIdToken();
      const resp = await fetch('/api/topup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          ...(idToken ? { Authorization: `Bearer ${idToken}` } : {}),
        },
        body: JSON.stringify({
          phone,
          countryCode,
          operator,
          amount: Number(amount),
          useLocalAmount: true,
        }),
        signal: controller.signal,
      });
      clearTimeout(timeout);

      // محاولة قراءة النص أولاً ثم تحويله إلى JSON إن أمكن
      const rawText = await resp.text();
      let data: any;
      try {
        data = JSON.parse(rawText);
      } catch {
        data = { success: false, message: rawText || 'فشل قراءة استجابة الخادم' };
      }

      if (resp.ok && data?.success) {
        toast({ title: 'تم الشحن', description: data?.message || 'تم تنفيذ عملية الشحن بنجاح' });
      } else {
        toast({ title: 'فشل الشحن', description: data?.message || 'حدث خطأ أثناء طلب الشحن', variant: 'destructive' });
      }
    } catch (err: any) {
      console.error('Topup request error:', err);
      if (err?.name === 'AbortError') {
        toast({ title: 'انتهت المهلة', description: 'الخادم لم يستجب في الوقت المحدد. تأكد أن السيرفر يعمل.', variant: 'destructive' });
      } else {
        toast({ title: 'خطأ في الاتصال', description: 'تعذر الاتصال بالخادم. حاول لاحقاً.', variant: 'destructive' });
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen p-4">
      <Card className="max-w-md mx-auto">
        <CardHeader>
          <CardTitle className="text-right">شحن رصيد الموبايل</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-right mb-1">رقم الموبايل</label>
              <Input
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                placeholder="01XXXXXXXXX"
                dir="ltr"
              />
              {!isValidEgyptPhone(phone) && phone.length > 0 && (
                <p className="text-xs text-red-600 mt-1 text-right">رقم غير صالح (10 أو 11 رقم)</p>
              )}
            </div>

            <div>
              <label className="block text-right mb-1">الشركة</label>
              <Select value={operator} onValueChange={setOperator}>
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="اختر الشركة" />
                </SelectTrigger>
                <SelectContent>
                  {egyptOperators.map((op) => (
                    <SelectItem key={op.value} value={op.value}>{op.label}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div>
              <label className="block text-right mb-1">المبلغ بالجنيه (EGP)</label>
              <Input
                type="number"
                inputMode="decimal"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                placeholder="مثال: 10"
                dir="ltr"
              />
            </div>

            <div className="flex items-center justify-between">
              <Button type="button" variant="outline" onClick={() => navigate(-1)}>
                رجوع
              </Button>
              <Button type="submit" disabled={loading}>
                {loading ? 'جارٍ التنفيذ...' : 'شحن'}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>

      {showConfirm && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-4 w-[90%] max-w-md">
            <h2 className="text-lg font-semibold text-right mb-2">تأكيد العملية</h2>
            <p className="text-right mb-4">هتشحن رقم {phone} مبلغ {amount} جنيه — تأكيد؟</p>
            <div className="flex gap-2 justify-end">
              <Button variant="outline" onClick={() => setShowConfirm(false)}>إلغاء</Button>
              <Button onClick={confirmTopup} disabled={loading}>{loading ? 'جارٍ التنفيذ...' : 'تأكيد'}</Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default TopUpPage;